.footer{
  border-top: 1px solid $mediumDark;
  padding: 36px 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    align-items: center;
    &-link{
      font-weight: 500;
      font-size: 16px;
      &:not(:last-child){
        margin-right: 32px;
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .footer{
    padding: 40px 0;
    &__inner{
      justify-content: center;
    }
    .hide-mob{
      display: none;
    }
  }
}


@media screen and (max-width: 450px){
  .footer{
    &__nav{
      &-link{
        &:not(:last-child){
          margin-right: 16px;
        }
      }
    }
  }
}
