.display1{
  font-size: 36px;
  line-height: 111%;
  font-weight: 400;
}

.display2{
  font-weight: 500;
  font-size: 24px;
  line-height: 133%;
}

.body-text,
.body-text2{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}

.body-text{
  font-size: 16px;
  line-height: 150%;
}

.body-text2{
  font-size: 14px;
  line-height: 143%;
}

@media screen and (max-width: 992px){
  .display1{
    font-size: 28px;
    line-height: 129%;
  }
}
