@import 'vars';
html {
    width: 100%;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

body {
    font-family: $font1;
    font-weight: 400;
    color: $text;
    background: $bg;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    width: 100%;
    max-width: 1276px;
    margin: 0 auto;
    padding: 0 30px;
    @media screen and (max-width: 992px) {
        padding: 0 24px;
        max-width: 848px;
    }
}

.page-block{
    padding: 84px 0 100px;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .page-block{
        padding: 44px 0 60px;
    }
}


@media screen and (max-width: 576px){
    .page-block{
        padding-top: 16px;
    }
}
