.profile{
  display: grid;
  grid-template-columns: calc(100% - 400px) 400px;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  &__top{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 32px;
    align-items: flex-end;
  }
  &__title{
    margin-top: 40px;
  }
}

.profile-content{
  padding: 40px 60px 60px;
}

.profile-sidebar{
  background-color: $mediumDark;
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 992px){
    .container{
      padding: 0;
    }
  }
  &__top{
    border-radius: 0 0 0 12px;
    border: $border;
    margin: -1px -1px 0 0;
    padding: 40px;
    background-color: $bg;
  }
  &__lk{
    display: flex;
    align-items: center;
  }
  &__btn{
    width: 100%;
    max-width: 320px;
    margin-top: 24px;
  }
  &__info{
    margin-top: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__sale{
    font-weight: 500;
  }
  &__bottom{
  }
  &__links{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    min-height: 0;
    overflow-y: auto;
    height: calc(100vh - 248px);
    padding: 40px 40px 28px;
  }
  &__link{
    display: flex;
    align-items: center;
    padding: 12px 0;
    &:not(:last-child){
      border-bottom: 1px solid #404056;
    }
    &:first-child,
    &--exit{
      border-top: 1px solid #404056;
    }
    &--exit{
      margin-top: auto;
    }
    &:hover{
      color: $warning;
    }
  }
}

.badge{
  padding: 16px 24px;
  display: flex;
  background-color: $mediumDark;
  border-radius: 8px;
  &__icon{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 16px;
    background-color: $darkGray;
  }
  &__title{
    font-weight: 500;
    font-size: 24px;
    display: block;
  }
  &__desc{
    color: $gray;
  }
}


@media screen and (max-width: 1470px){
  .profile{
    &__top{
      grid-template-columns: 1fr 1fr;
    }
    &__download{
      height: 100%;
    }
  }
}


@media screen and (max-width: 1100px){
  .profile-content{
    padding: 40px;
  }
}

