.logo{
  display: block;
  width: 252px;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 16px 0;
  &__inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left{
    display: flex;
    align-items: center;
    flex-shrink: 0;
    margin-right: 50px;
  }
  &__status{
    border-radius: 20px;
    margin-left: 16px;
    display: flex;
    align-items: center;
    padding: 0 12px;
    height: 20px;
  }
  .burger{
    display: none;
  }
  &__mob{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    &-wrap{
      flex-grow: 1;
      max-width: 722px;
    }
  }
  &__lang{
    flex-shrink: 0;
  }
  &__lk{
    display: flex;
    align-items: center;
    margin-left: 40px;
    font-weight: 500;
    font-size: 16px;
    &:hover{
      .header__lk-title{
        border-color: $warning;
        color: #fff;
      }
    }
    &-icon{
      width: 40px;
      height: 40px;
      background-color: $mediumDark;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      margin-right: 12px;
    }
    &-title{
      padding: 4px 0;
      border-bottom: 2px solid transparent;
      transition: all .3s;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  margin-right: auto;
  &__link{
    font-weight: 500;
    font-size: 16px;
    &:not(:last-child){
      margin-right: 42px;
    }
  }
}

.lang{
  position: relative;
  &__show{
    display: flex;
    align-items: center;
    height: 20px;
    padding: 0 12px;
    border-radius: 20px;
    background-color: $secondary;
    img{
      flex-shrink: 0;
      margin-left: 4px;
    }
    &:hover{
      cursor: pointer;
    }
  }
  &__hidden{
    position: absolute;
    width: 100%;
    min-height: 31px;
    top: 100%;
    margin-top: 4px;
    border-radius: 8px;
    padding: 5.5px 0;
    background-color: $primary;
    display: flex;
    flex-direction: column;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transform: translateY(20px);
    transition: all .3s;
  }
  &__item{
    display: block;
    text-align: center;
    &--active{
      display: none;
    }
    &:hover{
      color: $gray;
    }
  }
  &._active{
    .lang__hidden{
      opacity: 1;
      pointer-events: unset;
      transform: translateY(0);
    }
    .lang__show{
      img{
        transform: rotate(180deg);
      }
    }
  }
}


@media screen and (max-width: 1100px){
  .nav{
    &__link{
      &:not(:last-child){
        margin-right: 21px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    position: relative;
    &__mob{
      background-color: $darkGray;
      border: 2px solid $blueDark;
      border-radius: 12px;
      margin-right: -24px;
      flex-direction: column-reverse;
      padding: 40px 64px 40px 40px;
      &-wrap{
        position: absolute;
        top: 100%;
        right: 0;
        width: 0;
        max-width: calc(100% - 24px);
        overflow: hidden;
        transition: all .6s;
        opacity: 0;
        z-index: 10;
      }
    }

    .burger{
      flex-shrink: 0;
      width: 40px;
      height: 40px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      background-color: $mediumDark;
      span{
        border-radius: 10px;
        background-color: $warning;
        width: 20px;
        height: 2px;
        margin: 2px 0;
        transition: all .3s;
      }
    }

    &__lk{
      margin: 0 0 40px;
    }

    &._open{
      .header__mob-wrap{
        width: 100%;
        opacity: 1;
      }

      .burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translate(-4.5px,4px);
          }
          &:nth-child(2){
            opacity: 0;
          }
          &:nth-child(3){
            transform: rotate(45deg) translate(-4.5px,-4px);
          }
        }
      }
    }
  }

  .nav{
    margin: 40px 0 0;
    flex-direction: column;
    &__link{
      &:not(:last-child){
        margin: 0 0 40px;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__inner{
      align-items: unset;
    }
    &__left{
      flex-direction: column;
      margin: 0;
    }
    &__status{
      margin: 0 auto 0 60px;
    }
  }
}
