@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    height: 56px;
    border-radius: 8px;
    font-weight: 500;
    font-size: 16px;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-accent{
    background-color: $primary;
    color: $text;
    &:hover{
        background-color: $primaryHover;
        color: $text;
    }
    &:disabled{
        pointer-events: none;
        background-color: $gray;
        color: $text;
    }
}

.mbtn-accent-outline{
    color: $text;
    border: 2px solid $primaryHover;
    &:hover{
        background-color: $primaryHover;
        border-color: $primaryHover;
        color: $text;
    }
    &:disabled{
        pointer-events: none;
        background-color: $gray;
        border-color: $gray;
        color: $text;
    }
}
